
.component-container{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;

  .content-title{
    color: #adbac7;
    font-weight: 600 ;
    font-size: 14px;
  }

  .row-container{
    display: flex;
    align-items: center;
    margin-top: 10px;

    .input-container{
      display: flex;
      flex-direction: column;
      margin: 20px 40px 20px 0px;

      .input-label{
        color: #adbac3;
        font-weight: 500 ;
        font-size: 14px;
        margin-bottom: 10px;
      }

      .input{
        width: 100px;
        height: 25px;
        padding: 0px 15px;
        border-radius: 4px;
        border:1px solid #444c56;
        color: #adbac3;
        font-weight: 500 ;
        font-size: 14px;
      }

      .check-box{
        height: 25px;
        width: 25px;
        border-radius: 4px;
        border:1px solid #444c56;
        text-align: center;
        cursor: pointer;
      }

      .not-selected{
        background-color: transparent;
      }

      .selected{
        background-color: #2d333b;
        font-weight: 600;
      }
    }
  }

  .generete-btn-char-counter-container{
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    .generate-btn{
      width: 200px;
      margin-right: 40px;
    }
  }

  .text-area{
    box-sizing: border-box;
    height: calc(100% - 190px);
    width: 100%;
    background-color: transparent;
    padding:15px;
    border-radius: 4px;
    border:1px solid #444c56;
    color: #adbac3;
    font-weight: 500 ;
    font-size: 14px;
  }
}
